.produtos-grid {
  padding: 5rem 0;
}

.produtos-grid-container .title-container {
  margin-bottom: 4rem;
  text-align: center !important;
}

.produtos-grid-container .button-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 6.875rem;
}

.produtos-grid-container__grids {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr;
      grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
}

@media (min-width: 1200px) {
  .produtos-grid-container__grids {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
}
.produtos-grid-container__item {
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
}

.produtos-grid-container__item a {
  width: 100%;
  display: block;
}

.produtos-grid-container__item img {
  width: 100%;
  height: 100%;
  min-height: 150px;
  object-fit: cover;
  opacity: 0;
}

.produtos-grid-container__item img.tamanho-desk {
  display: none;
}

@media (min-width: 768px) {
  .produtos-grid-container__item img {
    min-height: 340px;
  }
}
@media (min-width: 1200px) {
  .produtos-grid-container__item img.tamanho-mobile {
    display: none;
  }
  .produtos-grid-container__item img.tamanho-desk {
    display: block;
  }
}
.produtos-grid-container__item .content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.produtos-grid-container__item .content h3 {
  color: #FFF;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
}

@media (min-width: 992px) {
  .produtos-grid-container__item .content h3 {
    font-size: 40px;
  }
}